.row{
    color:white;
    margin-left: 20px;
}


.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_posters::-webkit-scrollbar{
    display: none;
}





.row_poster {
    width: 100%;
    object-fit: contain;
    max-height: 100px;
    transition: transform 400ms;
    margin-right: 10px;

}

.row_poster:hover{
transform: scale(1.08);
}

.row_posterLarge{
    max-height: 300px;
}

.row_posterLarge:hover{
    transform: scale(1.09);

}